import React, { useState, useEffect, useRef} from "react";
import { db, auth } from "../firebaseConfig";
import { useNavigate } from "react-router-dom";
import firebase from "firebase/compat/app";
import "firebase/compat/database";
import "./css/LoginPage.css";

function LoginPage() {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    //
    const [errorMessage, setErrorMessage] = useState('');
    const [error, setError] = useState(false);

    const onEmailHandler = (e) => {
      setEmail(e.target.value);
    }
    const onPasswordHandler = (e) => {
      setPassword(e.target.value);
    }
  
    const isLogin = async (e) => {
    //   e.preventDefault();
        try {
            const result = await auth.signInWithEmailAndPassword(email, password);
            const token = await result.user.getIdToken();
            localStorage.setItem('token', token);
            localStorage.setItem('email', email);
            console.log('');
            //      
            const userRef = firebase.database().ref(`users/${result.user.uid}`);
            const snapshot = await userRef.once("value");
            const userData = snapshot.val();
        if (userData && userData.nickname) {
            localStorage.setItem('nickname', userData.nickname);
        }
        movePage('/');
      } catch (error) {
        setError(true);
          setErrorMessage('    .');
          console.log('');
          console.error("Error signing in:", error);
      }
    };
    const movePage = useNavigate();
  
    const signupClick = () => {
      movePage("/signup")
    }
    const loginClick = (e) => {
      e.preventDefault(); // form     
      if (email === '') {
        setError(true);
        setErrorMessage('  ');
      } else if (password === '') {
        setError(true);
        setErrorMessage('  ');
      } else {
        isLogin();
      }
    }
  
    return (
      <div className="login-background">
        <div className="login-box">
          <div className="login-title"></div>
          <form onSubmit={loginClick} className="inputForm">
            <div className="inputDiv">
              <input placeholder="" className="login-inputBox" type="text" value={email} onChange={onEmailHandler}></input>
            </div>
            <div className="inputDiv">
              <input className="login-inputBox passwordBox" placeholder="" type="password" value={password} onChange={onPasswordHandler}></input>
              {error && (
                <div className={`login-message`}>{errorMessage}</div>
              )}
            </div>
            <button type="submit" className="login-button"></button>
          </form>
          <button className="login-sign-button" onClick={signupClick}></button>
        </div>
      </div>
    );
  }

export default LoginPage;