import React, { useState, useEffect, useRef} from "react";
import { realtimeDB as db, auth } from "../firebaseConfig";
import { useNavigate } from "react-router-dom";
import firebase from "firebase/compat/app";
import "firebase/compat/database";
import "./css/PostPage.css";
import gpsicon from "../images/icons/maps.png";

function PostPage() {
    const titleInputRef = useRef(null);
    const contentInputRef = useRef(null);

    const [title, setTitle] = useState("");
    const [content, setContent] = useState("");

    // , 
    const subjectList = ["BOOK ", "BOOK ", "BOOK "];
    const ageList = ["", "10 ", "10", "20", "30", "40", "50", "60 "];
    const jobList = ["", "", "", "", "", "", "", "", "", "", ""];
    const hashtagList = ["", "", "", "", "", "", "", "", "", "",  "/ ", ""];
    const [subject, setSubject] = useState("BOOK ");
    const [age, setAge] = useState("");
    const [job, setJob] = useState("");
    const [hashtag, setHashtag] = useState("");
    const [town, setTown] = useState(localStorage.getItem("town"));
    const [lat, setLat] = useState(localStorage.getItem("lat"));
    const [lon, setLon] = useState(localStorage.getItem("lon"));

   
    const movePage = useNavigate();
  
    const handleTitle = (e) => {
        setTitle(e.target.value);
    }
    const handleContent = (e) => {
        setContent(e.target.value);
    }
    const handleSubject = (e) => {
        setSubject(e.target.value);
    };
    const handleAge = (e) => {
        setAge(e.target.value);
    };
    const handleJob = (e) => {
        setJob(e.target.value);
    };
    const handleHashtag = (e) => {
        setHashtag(e.target.value);
    };
    
    //      
    const [currentUser, setCurrentUser] = useState(null);

    //    
    useEffect(() => {
      const unsubscribe = auth.onAuthStateChanged((user) => {
        if (user) {
          setCurrentUser(user);
        } else {
          //       .
          movePage("/");

        }
      });
      return () => {
        unsubscribe();
      };
    }, []);
    // 
    const getKakaoAddress = async (latitude, longitude) => {
      const API_KEY = 'aa29e63fe27035e56f624827b0bbcb08';
      const API_URL = `https://dapi.kakao.com/v2/local/geo/coord2regioncode.json?x=${longitude}&y=${latitude}`;
    
      const response = await fetch(API_URL, {
        headers: {
          Authorization: `KakaoAK ${API_KEY}`,
        },
      });
      const data = await response.json();
      const address = data.documents[0].address_name;
      return address;
    };
  const townInfo = () => {
      try {
          navigator.geolocation.getCurrentPosition(
            async (position) => {
              const { latitude, longitude } = position.coords;
              const address = await getKakaoAddress(latitude, longitude);
              localStorage.setItem("town", address);
              localStorage.setItem("lat", latitude);
              localStorage.setItem("lon", longitude);
              setTown(address);
              console.log('add',address)
            },
            (error) => {
              console.error("Error fetching location:", error);
            }
          );
        } catch (error) {
          console.error("Error getting user location:", error);
        }
  }
    // 
    function getCurrentDate() {
        const today = new Date();
        const year = today.getFullYear();
        const month = String(today.getMonth() + 1).padStart(2, '0');
        const day = String(today.getDate()).padStart(2, '0');
      
        return `${year}.${month}.${day}`;
    }
    const postClick = async () => {
        if (town === ' '){
          townInfo();
        }
        else if(title === ''){
            titleInputRef.current.focus();
        }else if(content === ''){
            contentInputRef.current.focus();
        }else{
            postPost();
        }
    };
    const postPost = async () => {
        //  , ,      
        const postData = {
          title,
          content,
          subject,
          age,
          job,
          hashtag,
          town,
          lat,
          lon,
          user_id: currentUser.uid,
          email: localStorage.getItem("email"),
          nickname: localStorage.getItem("nickname"),
          date: getCurrentDate(),
        };
      
        try {
          //  realtime database postData  
          await db.ref("postList").push(postData);
          //      
          movePage("/");
        } catch (error) {
          console.error("  :", error);
        }
      };
  return (
    <div className="post-background">
        <div className="post-title"> </div>
        <div className="black-background">
            <div className="post-town" onClick={townInfo}>
                <img src={gpsicon} className="post-gps-icon" alt="gps icon"/>
                <div className="">{town}</div>
            </div>
            <select className="subject-input" onChange={handleSubject} value={subject}>
            {subjectList.map((item) => (
                <option value={item} key={item}>
                    {item}
                </option>
            ))}
            </select>
            <input className="title-input" ref={titleInputRef} placeholder=" " type="text" value={title} onChange={handleTitle}></input>
            <textarea className="content-input" ref={contentInputRef} placeholder=" " type="text" value={content} onChange={handleContent}></textarea >
            <div className="post-category">
      <div className="ca-form">
        <div className="ca-list"></div>
        <select className="select-from select-white" onChange={handleAge} value={age}>
          {ageList.map((item) => (
            <option value={item} key={item}>
              {item}
            </option>
          ))}
        </select>
      </div>
      <div className="ca-form">
        <div className="ca-list"></div>
        <select className="select-from select-white" onChange={handleJob} value={job}>
          {jobList.map((item) => (
            <option value={item} key={item}>
              {item}
            </option>
          ))}
        </select>
      </div>
      <div className="ca-form">
        <div className="ca-list"></div>
        <select className="select-from select-white" onChange={handleHashtag} value={hashtag}>
          {hashtagList.map((item) => (
            <option value={item} key={item}>
              {item}
            </option>
          ))}
        </select>
      </div>
      
    </div>
        </div>
        <button className="post-button" onClick={postClick}></button>
    </div>
  );
}

export default PostPage;