import React, { useState, useEffect, useRef} from "react";
import { db, auth } from "../firebaseConfig";
import { useNavigate } from "react-router-dom";
import firebase from "firebase/compat/app";
import "firebase/compat/database";
import "./css/SignupPage.css";

function SignupPage() {
  const movePage = useNavigate();
  const finishClick = () => {
    signupPost();
  }
  const dupClick = () => {
    dupCheck();
  }
  const emailInputRef = useRef(null);
  //  
  const [email, setEmail] = useState('test@test.com');
  const [password, setPassword] = useState('12345678');
  const [password_check, setPasswordCheck] = useState('12345678');
  const [nickname, setNickname] = useState('nicktest');

  //  
  
  const [emailMessage, setEmailMessage] = useState('');
  const [passwordLengthMessage, setPasswordLengthMessage] = useState('');
  const [passwordCheckMessage, setPasswordCheckMessage] = useState('');

  //
  
  const [isEmail, setIsEmail] = useState(false)
  const [isDup, setIsDup] = useState(false)
  const [isPassword, setIsPassword] = useState(false)
  const [isPasswordLength, setIsPasswordLength] = useState(false)
  const [isNickname, setIsNickname] = useState(false)
  
  const onChangeEmail = (e) => {
    const emailRegex =
      /([\w-.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/
    const emailCurrent = e.target.value
    setEmail(emailCurrent)

    if (!emailRegex.test(emailCurrent)) {
      setIsEmail(false)
      setIsDup(false)
      setEmailMessage('   .')
    } else {
      setIsEmail(true)
      setIsDup(false)
      setEmailMessage('  ')      
    }
  }
  //  
  useEffect(() => {
    if (isEmail){
      setIsEmail(true);
    }
    else{
      setIsEmail(false);
    }
  }, [isEmail]);
  useEffect(() => {
    if (password !==''){
      if(password.length >= 7 ){
        setPasswordLengthMessage('')
        setIsPasswordLength(true)
      }
      else{
        setPasswordLengthMessage(' 7 ')
        setIsPasswordLength(false)
      }
    }
    else{
        setPasswordLengthMessage('')
        setIsPasswordLength(false)
    }
  }, [password, isPasswordLength]);
  useEffect(() => {
    if (password === password_check){
      setPasswordCheckMessage(' .')
      setIsPassword(true)
    }else{
      setPasswordCheckMessage('  .')
      setIsPassword(false)
    }
  }, [password, password_check, isPassword]);
  useEffect(() => {
    if (nickname !==''){
      setIsNickname(true);
    }
    else if (nickname === ''){
      setIsNickname(false);
    }
  }, [nickname, isNickname]);
  
  const dupCheck = async () => {
    const usersRef = firebase.database().ref("users");
    let isDuplicate = false;
  
    await usersRef
      .orderByChild("email")
      .equalTo(email)
      .once("value", (snapshot) => {
        if (snapshot.exists()) {
          isDuplicate = true;
        }
      });
  
    if (isDuplicate) {
      console.log("");
      setEmailMessage("  .");
      setIsDup(false);
    } else {
      setEmailMessage("  .");
      setIsDup(true);
    }
  };
  const signupPost = async (e) => {
    try {
      const result = await auth.createUserWithEmailAndPassword(email, password);
      
      // Firestore   
      const userRef = db.collection("users").doc(result.user.uid);
      await userRef.set({
        email: email,
        uid: result.user.uid,
        nickname: nickname,
      });

      // Realtime Database   
      const userRealtimeRef = firebase.database().ref(`users/${result.user.uid}`);
      await userRealtimeRef.set({
        email: email,
        uid: result.user.uid,
        nickname: nickname,
      });

      movePage("/login");
    } catch (error) {
      console.error("Error creating user:", error);
    }
  };
  return (
    <div className="signup-background">
        <div className="signup-box">
        <div className="signup-title"></div>
        <div className="inputForm">
            <div className="inputDiv">
              <input placeholder="" className="signup-inputBox emailBox" type="text" value={email} onChange={(e) => onChangeEmail(e)} ref={emailInputRef}></input>
              <button className="dupBtn" onClick={dupClick} disabled={!(isEmail)}></button>
              {email.length > 0 && (
              <div className={`message ${isDup ? 'success' : 'error'}`}>{emailMessage}</div>
          )}
            </div>
            <div className="inputDiv">
              <input className="signup-inputBox passwordBox" placeholder="" type="password" value={password} onChange={(e) => setPassword(e.target.value)}></input>
              {password > 0 && (
              <div className={`message ${password.length > 6 ? 'success' : 'error'}`}>{passwordLengthMessage}</div>
          )}
            </div>
            <div className="inputDiv">
              <input className="signup-inputBox passwordBox" placeholder=" " type="password" value={password_check} onChange={(e) => setPasswordCheck(e.target.value)}></input>
              {password_check.length > 0 && (
              <div className={`message ${isPassword ? 'success' : 'error'}`}>{passwordCheckMessage}</div>
          )}
            </div>
            <div className="inputDiv">
              <input className="signup-inputBox nicknameBox" placeholder="" type="text" value={nickname} onChange={(e) => setNickname(e.target.value)}></input>
            </div>
        </div>
        <button className="finishBtn" onClick={finishClick} disabled={!(isEmail && isPassword && isPasswordLength && isNickname && isDup)}></button>
    </div>
    </div>
  );
}

export default SignupPage;